import { Component, Input } from '@angular/core';
import {
  Destination,
  getCountryName,
  PaymentCard,
  TravelNotice,
  TravelNoticeCountry,
} from '@backbase/cards-common-ang';

@Component({
  selector: 'bb-cards-travel-notice-confirm-step',
  templateUrl: './cards-travel-notice-confirm-step.component.html',
})
export class CardsTravelNoticeConfirmStepComponent {
  /**
   * Travel notice object.
   */
  @Input() travelNoticeItem: TravelNotice | undefined;
  /**
   * List of all selected cards.
   */
  @Input() selectedCards: PaymentCard[] | undefined;
  /**
   * List for all provided ISO countries/regions.
   */
  @Input() destinationList: Destination[] | undefined;

  getCountryName(travelNoticeCountry: TravelNoticeCountry) {
    return getCountryName(this.destinationList, travelNoticeCountry);
  }
}
