import { Component, forwardRef, Input, OnDestroy, OnInit } from '@angular/core';
import { ControlValueAccessor, FormBuilder, FormGroup, NG_VALUE_ACCESSOR } from '@angular/forms';
import { PaymentCard } from '@backbase/cards-common-ang';
import { Subscription } from 'rxjs';

@Component({
  selector: 'bb-cards-travel-notice-checkbox-cards',
  templateUrl: 'cards-travel-notice-checkbox-cards.component.html',
  providers: [
    {
      provide: NG_VALUE_ACCESSOR,
      // eslint-disable-next-line @angular-eslint/no-forward-ref
      useExisting: forwardRef(() => CardsTravelNoticeCheckboxCardsComponent),
      multi: true,
    },
  ],
})
export class CardsTravelNoticeCheckboxCardsComponent implements OnInit, OnDestroy, ControlValueAccessor {
  /**
   * Payment card to be displayed.
   */
  @Input() paymentCards: PaymentCard[] | undefined;

  checkBoxCardsForm: FormGroup | undefined;
  indeterminate = false;
  private parentSubscription: Subscription | undefined;
  private childrenSubscription: Subscription | undefined;

  private createCardsObjectWithDefaultValue(
    group: Array<any> = [],
    keyFinder: (item: any) => string,
    defaultValue: any,
  ): { [key: string]: any } {
    return group.reduce(
      (acc, cur) => ({
        ...acc,
        [keyFinder(cur)]: defaultValue,
      }),
      {},
    );
  }

  onChange: (newValue: Object | string | undefined) => void = () => {};

  onTouched: () => void = () => {};

  isIndeterminate = () => {
    if (this.checkBoxCardsForm) {
      const cardsIdsControl = (this.checkBoxCardsForm.controls['cardIds'] as FormGroup).controls;
      const elements = Object.keys(cardsIdsControl);
      const actives = elements.reduce((previous, item) => (cardsIdsControl[item].value ? previous + 1 : previous), 0);

      return actives !== 0 && actives < elements.length;
    }

    return false;
  };

  private readonly parentChange = (value: any) => {
    if (this.checkBoxCardsForm) {
      const cardsIdsControl = this.checkBoxCardsForm.controls['cardIds'] as FormGroup;
      const newValue = this.createCardsObjectWithDefaultValue(
        Object.keys(cardsIdsControl.controls),
        (item: string) => item,
        value,
      );
      cardsIdsControl.setValue(newValue);
    }
  };

  private readonly childChange = (cardIds: any) => {
    this.indeterminate = this.isIndeterminate();
    const result = Object.keys(cardIds || {}).filter((item: any) => cardIds[item] === true);
    this.onChange(result.map((item) => this.paymentCards && this.paymentCards.find((card) => card.id === item)));
  };

  private hasCardsSelected(control: any) {
    const cards = Object.keys(control.controls).find((item: any) => control.controls[item].value === true);

    // eslint-disable-next-line no-null/no-null
    return cards ? null : { empty: true };
  }

  private createForm() {
    const cardsFormGroup = this.createCardsObjectWithDefaultValue(this.paymentCards, (card: PaymentCard) => card.id, [
      false,
    ]);

    this.checkBoxCardsForm = this.fb.group({
      parent: [false],
      cardIds: this.fb.group(cardsFormGroup),
    });

    this.checkBoxCardsForm.controls['cardIds'].setValidators(this.hasCardsSelected);
  }

  writeValue(cards: PaymentCard[]): void {
    if (this.checkBoxCardsForm && cards && !!this.paymentCards) {
      const cardIds = this.checkBoxCardsForm.controls['cardIds'] as FormGroup;
      cards.forEach((item) => cardIds.controls[item.id].setValue(true));
      if (cards.length === this.paymentCards.length) {
        this.checkBoxCardsForm.patchValue({ parent: true });
      }
    }
  }

  registerOnChange(fn: () => void): void {
    this.onChange = fn;
  }

  registerOnTouched(fn: () => void): void {
    this.onTouched = fn;
  }

  ngOnInit() {
    this.createForm();

    if (this.checkBoxCardsForm) {
      const parentGroup = this.checkBoxCardsForm.controls['parent'];
      const childrenGroup = this.checkBoxCardsForm.controls['cardIds'];
      this.parentSubscription = parentGroup.valueChanges.subscribe(this.parentChange);
      this.childrenSubscription = childrenGroup.valueChanges.subscribe(this.childChange);
    }
  }

  ngOnDestroy() {
    if (this.parentSubscription) {
      this.parentSubscription.unsubscribe();
    }

    if (this.childrenSubscription) {
      this.childrenSubscription.unsubscribe();
    }
  }

  constructor(private readonly fb: FormBuilder) {}
}
