import { Component, EventEmitter, Input, Output } from '@angular/core';
import { Destination, getCountryName, TravelNotice, TravelNoticeCountry } from '@backbase/cards-common-ang';
import { TravelNoticeDatesHelperService } from '../../services/travel-notice-dates-helper.service';

@Component({
  selector: 'bb-cards-travel-notice-list-item',
  templateUrl: './cards-travel-notice-list-item.component.html',
})
export class CardsTravelNoticeListItemComponent {
  readonly hostRef = this;
  /**
   * Travel notice item.
   */
  @Input() travelNoticeItem: TravelNotice | undefined;
  /**
   * List for all provided ISO countries/regions.
   */
  @Input() destinationList: Destination[] | undefined;
  /**
   * EventEmitter for triggering delete event.
   */
  @Output() readonly delete = new EventEmitter();

  dialogOpen = false;

  areCardsOpen = false;

  getCountryName(travelNoticeCountry: TravelNoticeCountry) {
    return getCountryName(this.destinationList, travelNoticeCountry);
  }

  onDelete() {
    this.dialogOpen = true;
  }

  onCancel = () => {
    this.dialogOpen = false;
  };

  onConfirm = () => {
    this.delete.emit(this.travelNoticeItem);
    this.dialogOpen = false;
  };

  toggleCardsVisibility() {
    this.areCardsOpen = !this.areCardsOpen;
  }

  constructor(public helper: TravelNoticeDatesHelperService) {}
}
