import { ChangeDetectorRef, Component, Inject, OnInit, TemplateRef, ViewChild } from '@angular/core';
import {
  CardsBackendDestinations,
  CardsBackendError,
  CardsService,
  Destination,
  DestinationsServiceConfigToken,
  FullTravelNoticeModel,
  PaymentCard,
  TravelNotice,
  TravelNoticeList,
} from '@backbase/cards-common-ang';
import { AssetsResolver, AssetsResolverConfig, AssetsService } from '@backbase/foundation-ang/core';
import { NotificationService } from '@backbase/ui-ang/notification';
import { Observable, of } from 'rxjs';
import { catchError } from 'rxjs/operators';

const notificationTtl = 5000;
type NotificationMessage = string | TemplateRef<any>;
type NotificationModifier = 'success' | 'info' | 'warning' | 'error' | undefined;

@Component({
  selector: 'bb-cards-travel-notice-widget',
  templateUrl: 'cards-travel-notice-widget.component.html',
})
export class CardsTravelNoticeWidgetComponent implements OnInit {
  // eslint-disable-next-line
  @ViewChild('notificationOnCreate', { read: TemplateRef, static: true })
  notificationOnCreate: TemplateRef<any> | undefined;
  // eslint-disable-next-line
  @ViewChild('notificationOnDelete', { read: TemplateRef, static: true })
  notificationOnDelete: TemplateRef<any> | undefined;

  travelNoticeList: TravelNoticeList | undefined;
  paymentCards: PaymentCard[] | undefined;
  isLoading = true;
  /**
   * Error message in case something unexpected occurs.
   */
  error: CardsBackendError | undefined;

  destinationList: Observable<Destination[]> | undefined;

  private readonly config: AssetsResolverConfig = {
    widgetName: 'bb-cards-travel-notice-widget-ang',
    getURIFromAssetID: (assetId: string) => `${assetId}-img.svg`,
  };

  private readonly resolveAsset: AssetsResolver = this.assetsService.createAssetsResolver(this.config);

  get imageEmptyStateUrl() {
    return this.resolveAsset('travel-notice-empty-state');
  }

  ngOnInit() {
    this.loadTravelNotice();

    this.destinationList =
      this.destinationsService &&
      this.destinationsService.getDestinations().pipe(
        catchError((error: CardsBackendError) => {
          this.error = error;

          return of([]);
        }),
      );
  }

  submitTravelNotice(travelNoticeItem: TravelNotice) {
    this.isLoading = true;

    const submitTravelNoticeSuccess = () => {
      if (this.travelNoticeList) {
        this.loadTravelNotice();
        this.pushNotification(this.notificationOnCreate, 'success');
      }
    };

    this.cardsService.postTravelNotice(travelNoticeItem).subscribe(submitTravelNoticeSuccess);
  }

  onRemoveItem(travelNoticeItem: TravelNotice) {
    this.isLoading = true;

    const deleteTravelNoticeSuccess = () => {
      if (this.travelNoticeList) {
        this.loadTravelNotice();
        this.pushNotification(this.notificationOnDelete, 'success');
      }
    };

    this.cardsService.deleteTravelNotice(travelNoticeItem).subscribe(deleteTravelNoticeSuccess);
  }

  private loadTravelNotice() {
    this.cardsService
      .getTravelNotice()
      .pipe(
        catchError((error: CardsBackendError) => {
          this.error = error;

          return of({});
        }),
      )
      .subscribe((item: FullTravelNoticeModel) => {
        this.travelNoticeList = item.travelNoticeList;
        this.paymentCards = item.paymentCards;
        this.isLoading = false;
        this.cd.detectChanges();
      });
  }

  private pushNotification(header: NotificationMessage = '', modifier: NotificationModifier) {
    this.notificationService.showNotification({
      header,
      message: '',
      modifier,
      dismissible: true,
      ttl: notificationTtl,
    });
  }

  constructor(
    private readonly assetsService: AssetsService,
    private readonly cardsService: CardsService,
    private readonly notificationService: NotificationService,
    @Inject(DestinationsServiceConfigToken) private readonly destinationsService: CardsBackendDestinations | undefined,
    protected readonly cd: ChangeDetectorRef,
  ) {}
}
