import { Component, forwardRef, Input, OnDestroy, OnInit } from '@angular/core';
import { ControlValueAccessor, FormControl, NG_VALUE_ACCESSOR } from '@angular/forms';
import { Destination, TravelNoticeCountry } from '@backbase/cards-common-ang';
import { Subscription } from 'rxjs';
// eslint-disable-next-line @typescript-eslint/consistent-type-definitions
type DestinationDisplay = { name: string; value: string; parent?: string };

@Component({
  selector: 'bb-cards-travel-notice-country-selector',
  templateUrl: 'cards-travel-notice-country-selector.component.html',
  providers: [
    {
      provide: NG_VALUE_ACCESSOR,
      // eslint-disable-next-line @angular-eslint/no-forward-ref
      useExisting: forwardRef(() => CardsTravelNoticeCountrySelectorComponent),
      multi: true,
    },
  ],
})
export class CardsTravelNoticeCountrySelectorComponent implements ControlValueAccessor, OnInit, OnDestroy {
  private destinationsDisplay: DestinationDisplay[] | undefined;
  private fullDestinationList: Destination[] | undefined;
  private countryListSubscription: Subscription | undefined;
  destionationListControl = new FormControl([]);
  // eslint-disable-next-line
  @Input()
  set destinationList(value: Destination[]) {
    this.destinationsDisplay = [
      ...value.map((item) => ({
        name: item.name,
        value: item.code,
      })),
      ...value
        .filter((item) => item.regions !== undefined)
        .reduce(
          (acc, { regions = [], code: countryCode }) => [
            ...acc,
            ...regions.map((region) => ({
              name: `${region.name} (${countryCode})`,
              value: region.code,
              parent: countryCode,
            })),
          ],
          [] as DestinationDisplay[],
        ),
    ].sort((left, right) => {
      if (left.name === right.name) {
        return 0;
      }

      return left.name > right.name ? 1 : -1;
    });

    this.fullDestinationList = value;
  }

  get destinationList() {
    return this.fullDestinationList || [];
  }

  get destinationListDisplay(): DestinationDisplay[] {
    return this.destinationsDisplay || [];
  }

  private readonly updateCountryList = (destinations: DestinationDisplay[]) => {
    this.onChange(
      destinations.map((item) => ({
        country: item.parent ? item.parent : item.value,
        region: item.parent && item.value,
      })),
    );
  };

  onChange: (newValue: Object | string | undefined) => void = () => {};

  onTouched: () => void = () => {};

  writeValue(countries: TravelNoticeCountry[]): void {
    this.destionationListControl.setValue(
      countries.map(({ country, region }) => (region ? `${country}:${region}` : country)),
    );
  }

  registerOnChange(fn: any): void {
    this.onChange = fn;
  }

  registerOnTouched(fn: any): void {
    this.onTouched = fn;
  }

  ngOnInit() {
    this.countryListSubscription = this.destionationListControl.valueChanges.subscribe(this.updateCountryList);
  }

  ngOnDestroy() {
    if (this.countryListSubscription) {
      this.countryListSubscription.unsubscribe();
    }
  }
}
