import { Component, Directive, EventEmitter, Input, Output } from '@angular/core';
import { Destination, PaymentCard, TravelNotice, TravelNoticeList } from '@backbase/cards-common-ang';
import { BbTemplate } from '@backbase/foundation-ang/core';
// eslint-disable-next-line @typescript-eslint/consistent-type-definitions
type Page = 'create' | 'list' | 'edit';
// eslint-disable-next-line @typescript-eslint/consistent-type-definitions
type Operation = { goToPage: Page };

@Component({
  selector: 'bb-cards-travel-notice-container',
  templateUrl: './cards-travel-notice-container.component.html',
})
export class CardsTravelNoticeContainerComponent {
  readonly hostRef = this;
  /**
   * Empty state image url.
   */
  @Input() imageUrl: string | undefined = '';
  /**
   *  List of travel notices.
   */
  @Input() travelNoticeList: TravelNoticeList | undefined;
  /**
   * List for all provided ISO countries/regions.
   */
  @Input() destinationList: Destination[] | undefined;
  /**
   * Payment card to be displayed.
   */
  @Input() paymentCards: PaymentCard[] | undefined;
  /**
   * EventEmitter for triggering post event.
   */
  @Output() readonly createTravelNotice = new EventEmitter<TravelNotice>();
  /**
   * EventEmitter for triggering delete event.
   */
  @Output() readonly remove = new EventEmitter<TravelNotice>();
  page: Page = 'list';

  get hasItems() {
    return this.travelNoticeList && this.travelNoticeList.length > 0;
  }

  saveTravelNotice(travelNoticeItem: TravelNotice) {
    this.createTravelNotice.emit(travelNoticeItem);
    this.page = 'list';
  }

  setPage(operation: Operation) {
    this.page = operation.goToPage;
  }

  onRemove(travelNoticeItem: TravelNotice) {
    this.remove.emit(travelNoticeItem);
  }
}

@Directive({
  selector: '[bbTravelNoticeEmptyStateContainerCustomizable]',
})
export class TravelNoticeEmptyStateContainerCustomizableDirective extends BbTemplate<
  CardsTravelNoticeContainerComponent,
  undefined
> {}
