import { DatePipe } from '@angular/common';
import { Component, Directive, EventEmitter, Input, OnDestroy, OnInit, Output } from '@angular/core';
import { AbstractControl, FormBuilder, FormGroup, Validators } from '@angular/forms';
import { Destination, PaymentCard, TravelNotice } from '@backbase/cards-common-ang';
import { BbTemplate } from '@backbase/foundation-ang/core';
import { Subscription } from 'rxjs';

@Component({
  selector: 'bb-cards-travel-notice-create',
  templateUrl: './cards-travel-notice-create.component.html',
  providers: [DatePipe],
})
export class CardsTravelNoticeCreateComponent implements OnInit, OnDestroy {
  readonly hostRef = this;

  /**
   * Payment card to be displayed.
   */
  @Input() paymentCards: PaymentCard[] | undefined;
  /**
   * List for all provided ISO countries/regions.
   */
  @Input() destinationList: Destination[] | undefined;
  /**
   * Cancel form for create travel notice event emitter.
   */
  @Output() readonly abortCreation = new EventEmitter();
  /**
   * Submit for create travel notice form event emitter.
   */
  @Output() readonly save = new EventEmitter<TravelNotice>();

  currentStep = 1;
  travelNoticeSubscription: Subscription | undefined;
  arrivalDateSubcription: Subscription | undefined;
  departureDateSubscription: Subscription | undefined;
  createTravelNoticeForm: FormGroup | undefined;

  dialogOpen = false;
  displayAlert = false;

  currentForm: FormGroup | undefined;
  travelNoticeItem: Partial<TravelNotice> = {};

  next(nextForm: FormGroup) {
    if (this.currentForm) {
      this.currentForm.markAllAsTouched();
      this.currentForm.updateValueAndValidity();

      if (this.currentStep === 2) {
        this.displayAlert = this.currentForm.invalid;
      }

      if (this.currentForm.valid) {
        this.currentStep += 1;
        this.currentForm = nextForm;
      }
    }
  }

  back(prevForm: FormGroup) {
    this.currentStep -= 1;
    this.currentForm = prevForm;
  }

  onCancel(): void {
    this.dialogOpen = true;
  }

  onSubmitTravelNotice(): void {
    this.save.emit(this.travelNoticeItem as TravelNotice);
  }

  isFieldValid(fieldName: string) {
    const control = this.detailsGroup.get(fieldName);

    return !!control && (control.valid || control.untouched);
  }

  closeConfirmationDialog = () => {
    this.dialogOpen = false;
  };

  onAlertDismiss() {
    this.displayAlert = false;
  }

  confirmCancel = () => {
    this.closeConfirmationDialog();
    this.abortCreation.emit({ goToPage: 'list' });
  };

  ngOnInit() {
    if (!this.paymentCards) return;
    this.createTravelNoticeForm = this.fb.group({
      details: this.fb.group({
        destinations: [
          [],
          (control: AbstractControl) =>
            // eslint-disable-next-line , no-null/no-null
            control.value && control.value.length > 0 ? null : { required: 'item required' },
        ],
        departureDate: [''],
        arrivalDate: [''],
        contactNumber: ['', Validators.pattern('^[0-9()-+ ]*$')],
      }),
      cardIds: [this.paymentCards ? this.paymentCards : []],
    });

    this.createTravelNoticeForm.markAsPristine();

    this.travelNoticeSubscription = this.createTravelNoticeForm.valueChanges.subscribe(this.updateTravelNoticeValue);
    this.currentForm = this.detailsGroup;
    this.cardIdsGroup.setValidators(this.hasCardsSelected);
    this.addDateValidators();
  }
  /**
   *
   * temporary solution once datepicker is ready to validate ranges is release this method will be removed
   */
  addDateValidators() {
    if (!!this.createTravelNoticeForm && this.departureDate && this.arrivalDate) {
      this.departureDate.setValidators([Validators.required, this.dateValidator('arrivalDate', 'departureDate')]);

      this.arrivalDate.setValidators([
        Validators.required,
        this.currentDateValidator,
        this.dateValidator('arrivalDate', 'departureDate'),
      ]);

      this.departureDateSubscription = this.departureDate.valueChanges.subscribe(() => {
        this.arrivalDate.updateValueAndValidity({ emitEvent: false });
      });

      this.arrivalDateSubcription = this.arrivalDate.valueChanges.subscribe(() => {
        this.departureDate.updateValueAndValidity({ emitEvent: false });
      });
    }
  }

  ngOnDestroy() {
    if (this.travelNoticeSubscription) {
      this.travelNoticeSubscription.unsubscribe();
    }

    if (this.arrivalDateSubcription) {
      this.arrivalDateSubcription.unsubscribe();
    }

    if (this.departureDateSubscription) {
      this.departureDateSubscription.unsubscribe();
    }
  }

  private readonly updateTravelNoticeValue = (createTravelNoticeForm: any) => {
    const { details, cardIds: cards } = createTravelNoticeForm;

    if (cards) {
      this.travelNoticeItem = {
        ...this.travelNoticeItem,
        cards,
      };
    }

    if (details) {
      this.travelNoticeItem = {
        ...this.travelNoticeItem,
        destinations: details.destinations,
        contact: {
          phoneNumber: details.contactNumber + '',
        },
      };
    }

    if (this.departureDate.valid) {
      this.travelNoticeItem = {
        ...this.travelNoticeItem,
        departureDate: this.datePipe.transform(details.departureDate, 'yyyy-MM-dd') as string,
      };
    }

    if (this.arrivalDate.valid) {
      this.travelNoticeItem = {
        ...this.travelNoticeItem,
        arrivalDate: this.datePipe.transform(details.arrivalDate, 'yyyy-MM-dd') as string,
      };
    }

    this.displayAlert = false;
  };

  /**
   *
   * temporary solution once datepicker is ready to validate ranges is release this method will be removed
   */
  private dateValidator(fieldName1: string, fieldName2: string) {
    return () => {
      const formControl1 = this.detailsGroup.get(fieldName1) as AbstractControl;
      const formControl2 = this.detailsGroup.get(fieldName2) as AbstractControl;
      const date1 = new Date(formControl1.value).getTime();
      const date2 = new Date(formControl2.value).getTime();

      // eslint-disable-next-line , no-null/no-null
      return date1 < date2 ? { invalidDate: true } : null;
    };
  }

  private truncateDate(date: Date = new Date()) {
    const newDate = new Date(date);
    newDate.setHours(0, 0, 0, 0);

    return newDate;
  }

  private readonly currentDateValidator = (control: AbstractControl) => {
    const returnDate = new Date(control.value);
    const currentDate = this.truncateDate();

    // eslint-disable-next-line , no-null/no-null
    return returnDate.getTime() < currentDate.getTime() ? { invalidCurrentDate: true } : null;
  };

  private hasCardsSelected(control: AbstractControl) {
    const cards = control.value;

    // eslint-disable-next-line no-null/no-null
    return cards.length === 0 ? { empty: true } : null;
  }

  get detailsGroup(): FormGroup {
    return (this.createTravelNoticeForm as FormGroup).get('details') as FormGroup;
  }
  get cardIdsGroup(): FormGroup {
    return (this.createTravelNoticeForm as FormGroup).get('cardIds') as FormGroup;
  }

  get destinations(): AbstractControl {
    return this.detailsGroup.get('destinations') as AbstractControl;
  }
  get arrivalDate(): AbstractControl {
    return this.detailsGroup.get('arrivalDate') as AbstractControl;
  }
  get departureDate(): AbstractControl {
    return this.detailsGroup.get('departureDate') as AbstractControl;
  }
  get contactNumber(): AbstractControl {
    return this.detailsGroup.get('contactNumber') as AbstractControl;
  }

  constructor(private readonly fb: FormBuilder, private readonly datePipe: DatePipe) {}
}

@Directive({
  selector: '[bbTravelNoticeCreateCustomizable]',
})
export class TravelNoticeCreateCustomizableDirective extends BbTemplate<CardsTravelNoticeCreateComponent, undefined> {}

@Directive({
  selector: '[bbTravelNoticeDetailsStepCustomizable]',
})
export class TravelNoticeDetailsStepCustomizableDirective extends BbTemplate<
  CardsTravelNoticeCreateComponent,
  undefined
> {}

@Directive({
  selector: '[bbTravelNoticeSelectCardsCustomizable]',
})
export class TravelNoticeSelectCardsCustomizableDirective extends BbTemplate<
  CardsTravelNoticeCreateComponent,
  undefined
> {}

@Directive({
  selector: '[bbTravelNoticeConfirmStepCustomizable]',
})
export class TravelNoticeConfirmStepCustomizableDirective extends BbTemplate<
  CardsTravelNoticeCreateComponent,
  undefined
> {}

@Directive({
  selector: '[bbTravelNoticeStepperCustomizable]',
})
export class TravelNoticeStepperCustomizableDirective extends BbTemplate<CardsTravelNoticeCreateComponent, undefined> {}
