import { Component, Input, Output, EventEmitter, Directive } from '@angular/core';
import { TravelNoticeList, Destination, TravelNotice } from '@backbase/cards-common-ang';
import { BbTemplate } from '@backbase/foundation-ang/core';

@Component({
  selector: 'bb-cards-travel-notice-list',
  templateUrl: './cards-travel-notice-list.component.html',
})
export class CardsTravelNoticeListComponent {
  readonly hostRef = this;
  /**
   * list of all travel notices.
   */
  @Input() travelNoticeList: TravelNoticeList | undefined;
  /**
   * List for all provided ISO countries/regions.
   */
  @Input() destinationList: Destination[] | undefined;
  /**
   * EventEmitter for triggering post event.
   */
  @Output() readonly create = new EventEmitter();
  /**
   * EventEmitter for triggering delete event.
   */
  @Output() readonly deleteListItem = new EventEmitter();

  onShowCreateForm() {
    this.create.emit({ goToPage: 'create' });
  }

  onDeleteListItem(travelNoticeItem: TravelNotice) {
    this.deleteListItem.emit(travelNoticeItem);
  }
}

@Directive({
  selector: '[bbTravelNoticeListCustomizable]',
})
export class TravelNoticeListCustomizableDirective extends BbTemplate<CardsTravelNoticeListComponent, undefined> {}

@Directive({
  selector: '[bbTravelNoticeListItemCustomizable]',
})
export class TravelNoticeListItemCustomizableDirective extends BbTemplate<CardsTravelNoticeListComponent, undefined> {}
